@media screen and (max-width: 860px) {
  .about_section_container {
    max-width: 860px;
    width: 100%;
    padding: 0px 40px;
  }

  .about-uppersection {
    height: 650px;
  }

  .Title {
    margin-top: 220px;
    font-size: 78px;
    line-height: 100px;
  }

  .glow svg {
    left: 45%;
  }

  .origin-container {
    height: 730px;
  }

  .about_second h1 {
    font-size: 78px;
    width: 70%;
    line-height: 90px;
  }

  .origin_p_scroll {
    height: 630px;
  }

  .about_second p {
    margin-left: 12%;
    gap: 0px;
    font-size: 23px;
  }

  .drive-us-container {
    height: 630px;
  }

  .about_third h1 {
    font-size: 78px;
    line-height: 90px;
    width: 55%;
  }

  .about_third_p_scroll {
    height: 100%;
  }

  .about_third p {
    padding-bottom: 0px;
    margin-left: 12%;
    gap: 0px;
    font-size: 23px;
  }

  .team {
    flex-direction: column;
    margin-left: 15%;
  }

  .about_fifth {
    line-height: 100px;
  }
}

