    line-height: 60px;
  }

  .glow svg {
    left: 15%;
    top: 400px;
  }

  .origin-container {
    height: 830px;
  }
  .about_second h1 {
    font-size: 48px;
    line-height: 60px;
    margin: 60px 0px 20px;
    width: 100%;
  }

  .drive-us-container {
    height: 830px;
  }

  .about_second p {
    padding-bottom: 120px;
  }

  .about_third h1 {
    font-size: 48px;
    line-height: 60px;
    margin: 60px 0px 20px;
    width: 100%;
  }

  .align h1 {
    font-size: 50px;
    line-height: 60px;
    margin: 40px 0px 20px;
  }

  .team {
    margin-left: 0px;
  }

  .person {
    align-items: center;
    height: 100px;
  }

  .person img {
    height: 80px;
    border-radius: 40%;
  }

  .info {
    display: flex;
    flex-direction: column;
    margin-top: 0px;
    margin-left: 20px;
  }

  .info h4 {
    font-size: 23px;
    padding: 2px 0px;
  }

  .info p {
    font-size: 13px;
    padding: 2px 0px;
  }

  .about_fifth {
    line-height: 30px;
    font-size: 20px;
    margin: 10px 0px;
  }

  .other-container {
    height: 50vh;
  }
}
